import commonList from '@/components/common/common-list.vue';
import bottomInput from '@/components/common/bottom-input.vue';
import moreShare from '@/components/common/more-share.vue';
export default {
	components:{
		commonList,
		bottomInput,
		moreShare
	},
	data() {
		return {
			// 当前帖子信息
			info:{
				username: "昵称",
				userpic: "/static/default.png",
				newstime: "2020-01-21 下午15:51",
				isFollow: false,
				title: "我是标题",
				titlepic: "/static/demo/1.jpg",
				support: {
					type: "support",
					support_count: 1,
					unsupport_count: 2
				},
				comment_count: 2,
				share_count: 2,
				content:"这是一本异世穿越小说，现在的穿越到异世的小说很多，也就是说这本小说就是个老套路文",
				images:[{
					url:"http://static.zongheng.com/upload/cover/2017/07/1500036390863.jpg"
				},{
					url:"http://static.zongheng.com/upload/cover/c2/da/c2da0505bb634c19d140941497242342.jpeg"
				}]
			}
		}
	},
	onLoad(e) {
		// 初始化
		if (e.detail) {
			this.__init(JSON.parse(e.detail))
		}
	},
	computed: {
		imageList(){
			return this.info.images.map(item=>item.url)
		}
	},
	onNavigationBarButtonTap() {
		this.$refs.share.open()
	},
	onBackPress() {
		this.$refs.share.close()
	},
	methods: {
		__init(data){
			// 修改标题
			uni.setNavigationBarTitle({
				title:data.title
			})
			// 请求api
		},
		// 点击评论
		doComment(){
			
		},
		// 点击分享
		doShare(){
			
		},
		// 关注
		follow(){
			this.info.isFollow = true
			uni.showToast({
				title: '关注成功'
			});
		},
		// 顶踩操作
		doSupport(e){
			// 之前操作过
			if (this.info.support.type === e.type) {
				return uni.showToast({
					title:'你已经操作过了',
					icon:'none'
				})
			}
			let msg = e.type === 'support' ? '顶' : '踩'
			// 判断之前有没有操作过
			if (this.info.support.type === '') {
				this.info.support[e.type+'_count']++
			}else if (this.info.support.type === 'support' && e.type === 'unsupport'){
				// 顶 - 1
				this.info.support.support_count--;
				// 踩 + 1
				this.info.support.unsupport_count++;
			}else if (this.info.support.type === 'unsupport' && e.type === 'support'){
				// 顶 + 1
				this.info.support.support_count++;
				// 踩 - 1
				this.info.support.unsupport_count--;
			}
			this.info.support.type = e.type
			uni.showToast({ title: msg + '成功' });
		},
		// 预览图片
		preview(index){
			uni.previewImage({
				current: index,
				urls: this.imageList
			});
		},
		// 提交评论
		submit(data){
			console.log(data);
		}
	}
}
